@import url('https://fonts.googleapis.com/css?family=Russo+One&display=swap');
$borderWindow: 1px solid black;
$technologyFontSize: 18px;

.App {
  text-align: center;
}

#editor-container, #previewer-container {
  text-align: center;
} 

#editor-title, #previewer-title {
  font-family: 'Russo One', sans-serif;
}

.fa-sass {
  color: rgb(255, 172, 186);
  font-size: $technologyFontSize;
}

.fa-react {
  color: lightblue;
  font-size: $technologyFontSize;
}

#editor {
  width: 60%;
  height: 33%;
  border: $borderWindow;
}

#preview {
  width: 80%;
  text-align: left;
  margin: 0px 100px;
  padding: 0px 20px;
  border: $borderWindow;
  border-radius: 5px;
  box-shadow: 5px 5px 5px;
}

#footer {
  margin-top: 30px;
}